@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script:400,700&display=swap);
.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: auto;
}

.element {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 55vw;
  height: 25vh;
}

.box {
  margin-top: 15px;
  padding: 30px;
  border: 1px solid #dec79b;
  display: flex;
  flex-direction: column;
  align-items: center;
}

img {
  width: 100px;
}

h1 {
  font-size: 60px;
  color: #fafafa;
  white-space: nowrap;
}

h3 {
  font-size: 32px;
  font-weight: 400;
  color: rgb(153, 153, 153);
}

.element > * {
  margin-bottom: 15px;
}

.description {
  display: flex;
  flex-direction: column;
}

.description > * {
  margin-bottom: 5px;
}

.icons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.icons > * {
  margin: 0 4px;
  color: #dec79b;
}

.icons > *:hover {
  opacity: 0.6;
  cursor: pointer;
  transition: 0.6s;
}

.header {
  background-color: #1a1a1f;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.2);
  position: fixed;
  width: 100%;
  z-index: 3;
  font-weight: bold;
  font-size: 16px;
}

.header img {
  width: 200px;
  height: 100%;
}

.header img:hover {
  opacity: 0.6;
  transition: 0.2s;
}

.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  background-color: #1a1a1f;
}

.header li a {
  color: rgb(153, 153, 153);
  display: block;
  padding: 20px 20px;
  border-right: 1px solid #dec79b;
  text-decoration: none;
}

.header li a:hover,
.header .menu-btn:hover {
  color: #f7f7f7;
  transition: 0.2s;
}

.header .logo {
  display: block;
  float: left;
  padding: 10px 20px;
}

/* menu */

.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height 0.2s ease-out;
}

/* menu icon */

.header .menu-icon {
  cursor: pointer;
  display: inline-block;
  padding: 28px 20px;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header .menu-icon .navicon {
  background: #dec79b;
  display: block;
  height: 2px;
  position: relative;
  transition: background 0.2s ease-out;
  width: 18px;
}

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  background: #dec79b;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.header .menu-icon .navicon:before {
  top: 5px;
}

.header .menu-icon .navicon:after {
  top: -5px;
}

/* menu btn */

.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 240px;
}

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.header .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
}

/* 48em = 768px */
@media (max-width: 48em) {
  .header .menu-icon {
    position: absolute;
    top: 6px;
    right: 10px;
  }

  .header .logo {
    width: 100%;
    text-align: center;
  }
}

@media (max-width: 31.25em) {
  .header img {
    width: 150px;
    height: 100%;
  }

  .header .menu-icon {
    top: 0px;
  }
}

@media (min-width: 48em) {
  .header li {
    float: left;
  }
  .header li a {
    padding: 20px 30px;
  }
  .header .menu {
    clear: none;
    float: right;
    max-height: none;
  }
  .header .menu-icon {
    display: none;
  }
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}
*:focus {
  outline: 0;
}
html,
body,
#root {
  height: 100%;
}
body {
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  background: #18181e;
  color: #f7f7f7;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  font: 17px 'Roboto', sans-serif;
  line-height: 1;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  font: 17px 'Roboto', sans-serif;
  line-height: 1;
}
input::-ms-input-placeholder, textarea::-ms-input-placeholder {
  font: 17px 'Roboto', sans-serif;
  line-height: 1;
}
body,
input,
textarea,
button,
input::placeholder,
textarea::placeholder {
  font: 17px 'Roboto', sans-serif;
  line-height: 1;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
button {
  cursor: pointer;
}

