.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: auto;
}

.element {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 55vw;
  height: 25vh;
}

.box {
  margin-top: 15px;
  padding: 30px;
  border: 1px solid #dec79b;
  display: flex;
  flex-direction: column;
  align-items: center;
}

img {
  width: 100px;
}

h1 {
  font-size: 60px;
  color: #fafafa;
  white-space: nowrap;
}

h3 {
  font-size: 32px;
  font-weight: 400;
  color: rgb(153, 153, 153);
}

.element > * {
  margin-bottom: 15px;
}

.description {
  display: flex;
  flex-direction: column;
}

.description > * {
  margin-bottom: 5px;
}

.icons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.icons > * {
  margin: 0 4px;
  color: #dec79b;
}

.icons > *:hover {
  opacity: 0.6;
  cursor: pointer;
  transition: 0.6s;
}
